#hamburger {
  position: relative;
  width: 20px;
  height: 20px;
  z-index: 3;
}

#hamburger span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  width: 24px;
  height: 3px;
  background: black;
  transition: transform 0.25s;
}

#hamburger span::before {
  position: absolute;
  right: 0;
  display: inline-block;
  content: "";
  width: 18px;
  height: 3px;
  background: black;
  transform: translateY(-4px);
  transition: transform 0.25s;
}

#hamburger active span {
  transform: rotate(45deg);
}

#hamburger active span::before {
  top: unset;
  width: 24px;
  transform: rotate(-90deg);
}

#hamburger.active span {
  
  background: #cdcaa2;
}

#hamburger.active span::before {
  
  background: #cdcaa2;
}

