.menu {
  
  background: #000;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  transition: all 0.5s ease-in-out;
  clip-path: circle(0%);
  transform: translateY(50%) scale(0);
  animation: none;
  
  z-index: -1;
}

.menu.expanded {
  animation: revealCircular 0.5s forwards;
  z-index: 2;
}

@keyframes revealCircular {
  0% {
    clip-path: circle(0%);
    transform: translateY(50%) scale(0);
  }

  100% {
    clip-path: circle(75%);
    transform: translateY(0) scale(1);
  }
}

