section.header {
  width: 100%;
  height: 100vh;
  /* background-color: #FAEFCD; */
  z-index: 1;
}

.header::before {
  content: '';
  background-color: bisque;
  height: 30vh;
  position: relative;
}

.header::after {
  transform: translateX(-10vw);
  z-index: 0;
  content: '';
  position: absolute;
  width: 113%;
  height: 85vh;
  left: 0;
  top: -2vh;
  background-image: url('./Components/Resources/svg-path.svg');
  background-repeat: no-repeat;
}

.shapes {
  position: relative;
}


.shapes img {
  z-index: 2;
